.resume-container {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    min-height: fit-content;
    margin: 120px 0;

  }
  #secondary-handles{
    width: 15em;
    border: 1px solid grey;
    border-radius: 15px;
    margin-top: 1rem;
    margin-left: 30%;

  }
  .resume-card {
    display: flex;
    height: 420px;
    width: 1000px;;
    /* margin: 80px 0; */
  }
  
  .resume-bullets {
    box-shadow: 15px 0 9px -15px #1f2235;
    height: 100%;
    width: 320px;
  }
  
  .resume-bullet-details {
    flex-grow: 1;
    width: 600px;
    overflow: hidden;
    padding: 0 0 0 80px;
  }
  
  .experience-description {
    margin: 10px 0 0 0;
    text-align: justify;
  }
  
  .resume-description-text {
    font-size: 14px;
  }
  
  .resume-details-carousal {
    transition: transform 1s ease-out;
  }
  
  .bullet-container {
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    position: relative;
  }
  
  .bullets {
    width: 86%;
    position: relative;
    z-index: 2;
  }
  
  .bullet {
    display: flex;
    align-items: center;
    cursor: pointer;
    background-color: #1f2235;
    height: 40px;
    margin: 15px 0;
    padding: 0 8px;
    border-radius: 20px;
    width: 30px;
    transition: width 0.6s ease;
  }
  
  .bullet-label {
    font-size: 14px;
    white-space: nowrap;
    font-family: "Poppins SemiBold";
  }
  
  .bullet-logo {
    height: 16px;
    margin: 0 30px 0 0;
  }
  
  .bullet-icons {
    width: 34px;
    height: 100%;
    z-index: 1;
    background-color: #1f2235;
    position: absolute;
  }
  
  .selected-bullet {
    color: var(--primary);
    width: 100%;
  }
  
  .resume-screen-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 420px;
    width: 100%;
  }
  
  .resume-heading {
    display: flex;
    flex-direction: column;
  }
  
  .resume-main-heading {
    position: relative;
    display: flex;
    justify-content: space-between;
    font-weight: bolder;
    margin-top: 2rem;
  }
  
  .resume-main-heading span {
    font-size: 16px;
    font-family: "Poppins SemiBold";
  }
  .resume-main-heading span a {
    text-decoration: none;
    color: var(--dark-primary);
  }
  .resume-main-heading span a:hover{
    color: var(--primary);
  }
  
  
  .experience-description {
    max-width: 100%;
  }
  
  .heading-bullet {
    position: absolute;
    left: -30px;
    height: 15px;
    width: 15px;
    top: 5px;
    border-radius: 50%;
    background-color: var(--primary);
  }
  
  .resume-sub-heading {
    font-size: 14px;
    font-family: "Poppins SemiBold";
    font-weight: 700;
    color: var(--black);
  }
  
  .resume-heading-description {
    font-size: 14px;
    text-align: justify;
  }
  
  .heading-date {
    background-color: var(--primary);
    padding: 4px 12px;
    font-size: 14px;
    border-radius: 14px;
    color: var(--white);
  }
  
  .skill-parent {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 50%;
  }
  
  .programming-skills-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .skill-percentage {
    position: relative;
    height: 15px;
    width: 70%;
    background-color: var(--primary);
  }
  
  .skill-percentage .active-percentage-bar {
    height: 15px;
    transition: 0.8s ease;
    position: absolute;
    background-color: #1f2235;
  }
  
  .skill-parent span {
    font-family: "Poppins Bold";
    font-size: 16px;
    color: var(--primary);
  }

  /* IPAD RESPONSIVENESS */
@media only screen and (max-width: 1023px) {
    .resume-content {
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 1rem;
    }
    .bullets {
      width: 90%;
    }
    .resume-heading-description p{
        font-size: 0.8rem;
    }
    .resume-main-heading{
        font-size: 0.9rem;
    }
    .resume-card {
      height: fit-content;
      display: flex;
      flex-direction: column;
      margin: 25px 0;
      align-items: center;
      width: 90%;
    }
    .programming-skills-container{
        margin-bottom: 1.5rem;
    }
    .resume-bullet-details {
      height: 420px;
      padding: 0 ;
      width: fit-content;
    }
    .heading-bullet{
        display: none;
    }
    .resume-bullets {
      margin: 30px 0;
      width: 100%;
    }
  
    .selected-bullet {
      width: 100%;
    }
  }
  